<template>
	<div>
		<div class='con con-head'>
			<ul>
				<li v-ripple v-for='(item,index) in top_action_icons' @touchstart='press("top_action_icons",index)' @touchend='pressup("top_action_icons",index)'>
					<img class='icon' :src='item.icons[item.selected]' />
					<div :style='{color:item.selected==1?"rgb(0,209,255)":"#fff"}'>{{item.text}}</div>
				</li>
			</ul>
			<v-touch class='nav-bar' v-ripple-btn @tap='openLocal($event)'>
				<img :src='phone' class='icon' />
				<span>本地音乐</span>
				<v-touch tag='img' :src='play.icons[play.selected]' class='small-icon' @touchstart.stop='press("play")' @touchend.stop='pressup("play")'></v-touch>
				<i class='iconfont icon-right _right'></i>
				<span class='_right'>{{total}}首</span>
			</v-touch>
		</div>
		<div class='con con-nav'>
			<ul>
				<li v-ripple v-for='(item,index) in center_nav_icons' @touchstart='press("center_nav_icons",index)' @touchend='pressup("center_nav_icons",index)'>
					<img class='icon center_nav_icon' :src='item.icons[item.selected]' />
					<div :style='{color:item.selected==1?"rgb(0,209,255)":"#fff"}'>{{item.text}}</div>
				</li>
			</ul>
		</div>
		<div class='con'>
			<v-touch class='nav-bar action-bar' v-ripple-btn @tap='toggleActionNav'>
				<div>
					<img :src='earphone' class='icon' />
				</div>
				<div class='underline' :style='{"border-bottom-width":ul_close?"1px":"0px",transition:ul_close?"all 0.4s":"0s"}'>
					<span>工具</span>
					<i class='iconfont icon-down _right' :style='{transform:ul_close?"translateY(1.12vh) rotate(0deg)":"translateY(1.12vh) rotate(180deg)"}'></i>
				</div>
			</v-touch>
			<ul id='action-nav' :class='{close:ul_close,open:!ul_close}'>
				<li v-ripple>
					<i class='iconfont icon-run'></i>
					<div class='light_color'>跑步</div>
				</li>
				<li v-ripple>
					<i class='iconfont icon-pass'></i>
					<div class='light_color'>传歌</div>
				</li>
				<li v-ripple>
					<i class='iconfont icon-preview'></i>
					<div class='light_color'>扫描</div>
				</li>
				<li v-ripple>
					<i class='iconfont icon-song'></i>
					<div class='light_color'>挑歌</div>
				</li>
				<li v-ripple>
					<i class='iconfont icon-sound'></i>
					<div class='light_color'>铃声</div>
				</li>
				<li v-ripple>
					<i class='iconfont icon-identify'></i>
					<div class='light_color'>识曲</div>
				</li>
			</ul>
		</div>
		<div class='con'>
			<v-touch class='nav-bar action-bar' v-ripple-btn>
				<div>
					<img :src='game' class='icon' />
				</div>
				<div class='underline'>
					<span>游戏</span>
				</div>				
			</v-touch>
		</div>
		<div class='con '>
			<v-touch class='nav-bar action-bar' v-ripple-btn>
				<div>
					<img :src='generalize' class='icon' />
				</div>
				<div class='underline'>
					<span>推广</span>
				</div>	
			</v-touch>
			<div class='underline'></div>
		</div>
	</div>
</template>

<script type="es6">
	import ripple from '../../directives/ripple.js';
	import rippleBtn from '../../directives/ripple-btn.js';
	import { mapState } from 'vuex'	
	export default {
		data(){
			return{
				ul_close:true
			}
		},
		computed:{
			...mapState([
				'top_action_icons',
				'phone',
				'play',
				'arrow_right',
				'total',
				'center_nav_icons',
				'earphone',
				'game',
				'generalize'
			])
		},
		directives:{
			ripple,
			rippleBtn
		},
		methods:{
			press(key,index){
				this.$store.commit('press',{key,index});
			},
			pressup(key,index){
				this.$store.commit('pressup',{key,index});
			},
			autoPlay(){
				console.log(1);
				return;
			},
			openLocal(event){
				if(event.target.nodeName=='IMG'){
					this.autoPlay();
					return
				}else{

				}
				
			},
			toggleActionNav(){
				this.ul_close=!this.ul_close;

			}
		}
	}
</script>

<style scoped lang="scss">
.con{
	padding:0px 4vw;
}
.con-head{
	border-bottom:1.2vh solid rgb(1,68,110);
}
.con-nav{
	border:none;
	ul{
		padding:3.2vh 8vw;
		div{
			margin-top:1vh;
		}
	}
}
ul{
	padding: 2.5vh 3vw;
	border-bottom-width: 1px;
	border-bottom-style: solid;
	display: flex;
	flex-direction: row;
	justify-content: space-between;
	&#action-nav{
		transition:height 0.4s linear, padding 0.4s linear;
	}
	&.open{
		padding:0px 3vw 2vh 3vw;
	}
	&.close{
		height:0;
		opacity:0;
		overflow:hidden;
		padding:0px 3vw;
	}
	li{
		text-align: center;
		position:relative;
		transition:all 0.4s linear;
		div{
			text-overflow:hidden;
			white-space:nowrap;
		}
		i{
			font-size:5.7vw;
			color:rgb(179,197,211);
		}
	}
	.icon{
		width: 13.5vw;
		height: auto;
	}
	.center_nav_icon{
		width:15.5vw;
	}
}
.icon{
	width:4.5vw;
}
.small-icon{
	width:4.5vw;
}
.nav-bar{
	padding:2.7vh 2vw;
	img{
		vertical-align:middle;
		margin-right:3vw;
	}
	span{
		display:inline-block;
		transform:translateY(0.5vh);
		margin-right:2vw;
		&._right{
			font-size:3.6vw;
			transform:translateY(0.92vh);
			color:rgb(138,155,175);
		}
	}
	i._right{
		font-size:3.6vw;
		transform:translateY(1.12vh);
	}
}
.action-bar{
	padding:0vh 2vw;
	display:flex;
	img{
		width:7vw;
		margin-right:2vw;
		padding-top:1.5vh;
	}
}
.underline{
	display:inline-block;
	padding:1.5vh 0px 2.5vh 0;
	flex-grow:1;
	border-bottom-style:solid;
	border-bottom-width:1px;
}
</style>


